import { Component, OnInit } from '@angular/core';
// import { FaqService } from '../../admin/frontend/faq/services/faq.service';
// import { FaqCategoryService } from '../../admin/frontend/faq/services/faq-category.service';
import { CmsService } from '../services/cms.service';

@Component({
  selector: 'app-support',
  templateUrl: './support.component.html',
  styleUrls: ['./support.component.scss']
})
export class SupportComponent implements OnInit {

  public faqcategoryList: any;
  public faqList: any;
  public list: any[] = [];
  public search: String;

  constructor(
    private faqService: CmsService,
    private faqCategoryService: CmsService,
  ) { }

  ngOnInit() {
    this.getCategory();
  }

  getCategory() {
    this.faqCategoryService.getFaqCategory()
    .subscribe(res => {
      this.faqcategoryList = res;
      this.getFaq();
    });
  }

  getFaq() {
    this.faqService.getFaq()
    .subscribe(res => {
      this.faqList = res;
      for (const prop of res) {
        if (this.list[prop.faqcategoryid] === undefined) {
          this.list[prop.faqcategoryid] = [];
        }
        this.list[prop.faqcategoryid].push(prop);
      }
    });
  }

}
